.scrollbars {
  position: relative;
  display: flex;
  overflow: hidden !important;
}

.scrollbars__scroll-wrapper {
  margin-right: -20px;
  padding-right: 20px;
  margin-bottom: -20px;
  padding-bottom: 20px;
  overflow-Y: scroll;
}

.scrollbars__bar-wrapper-x,
.scrollbars__bar-wrapper-y {
  position: absolute;
  border-radius: 6px;
  background: rgba(0,0,0, .1);
}

.scrollbars__bar-wrapper-y {
  right: 0;
  top: 6px;
  bottom: 6px;
  width: 6px;

  .scrollbars__bar {
    top: 0;
    left: 50%;
    transform: translateX(-50%);
  }
}

.scrollbars__bar-wrapper-x {
  bottom: 0;
  left: 6px;
  right: 6px;
  height: 6px;

  .scrollbars__bar {
    left: 0;
    top: 50%;
    transform: translateY(-50%);
  }
}

.scrollbars__bar {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0, .25);
  border-radius: 6px;
}

.scrollbars--hoverable {
  .scrollbars__bar-wrapper-x,
  .scrollbars__bar-wrapper-y {
    opacity: 0;
    transition: opacity 300ms ease 200ms;
  }
}

.scrollbars--hoverable:hover .scrollbars__bar-wrapper-y,
.scrollbars--hoverable:hover .scrollbars__bar-wrapper-x,
.scrollbars--hoverable .scrollbars__bar-wrapper--grabbed {
  opacity: 1;
}
